/* src/YouTubePlayer.css */
.youtube-player-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  
  .youtube-video {
    border: 2px solid #ccc;
    border-radius: 10px;
    overflow: hidden;
  }
  
  .error-message {
    color: red;
    font-size: 1.2em;
    text-align: center;
    margin-top: 20px;
  }
  
  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .spinner {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  /* Additional styles to hide the 'Watch on YouTube', 'Watch Later', and 'Share' icons */
  .video-wrapper {
    position: relative;
    width: 640px;
    height: 390px;
  }
  
  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  /* Hide 'Watch on YouTube' link, 'Watch Later', and 'Share' icons */
  .youtube-iframe::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    background: white; /* Match this to the iframe's background color */
    pointer-events: none;
  }
  